@import url(./font.css);

.dot{
    opacity: 0;
    width: 180px;
    padding: 10px;
    color:  white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: Next;
    z-index: 100;

    /*TEMP*/
    top: 336px;
    left: 353px;
}
.dot-item-span{
    font-size: 18px;
    color: rgb(217, 217, 217);
}
.dot-item-title{
    font-family: Next;
    font-weight:lighter;
    font-size: 25px;
    margin-bottom: 10px;
}

.dot-item-icon {
    border-radius: 50%;
    display: grid;
    padding: 10px;
    place-content: center;
    transition: all 0.5s ease;
    pointer-events: none;
    z-index: -1;
  }
  
  .dot-item-icon  img{
    filter: invert(1);
  }
