@import url(./font.css);

div.section.active {
  display: flex;
  z-index: 100;
  flex-direction: column;
  align-items: center;
  width: 65%;
  filter: brightness(2);
  transition: width 1.5s ease; /* Agregamos una transición suave */
}

.dots-container:has(.active) .section:not(.active) {
  width: 200px;
  /* Estilo aplicado a los elementos sin la clase "active" cuando al menos uno de ellos la tiene */
  transition: width 50s ease, opacity 1s ease;
  pointer-events: none;
  /* Agregamos una transición suave */
  opacity: 0;
}

.network{
  display: none;
}

.dots-container .activeDot {
  /* Use "opacity" instead of "display" for fading effect */
  display: block !important;
}

.dots-container .desactiveDot {
  /* Use "opacity" instead of "display" for fading effect */
  display: none !important;
}

.sub-page-active{
  display:  grid !important;
  grid-template-rows:repeat(2, 1fr) ;
  grid-template-columns: 350px auto;
  grid-template-areas: 
  "image title"
  "image description"
  "image button";
}

.sub-page-active .content-title{
  grid-area: title;
  text-align: left;
  margin-left: 75px;
}
.sub-page-active .content-description{
  grid-area: description;
  text-align: left;
  margin-right: 0;

}
.sub-page-active .content-subpage-list{
  grid-area: button;
  justify-content: flex-start;
}
.sub-page-active .content-subpage.item-container{
  margin-left: 80px !important;
  align-self: left;
}


.sub-page-active .content-image{
  grid-area: image;
  width: 100%;
}





div.section.active {
  animation: fadeIn 5.5s cubic-bezier(0.755, 0.05, 0.855, 0.06); /* Iniciar la animación de fade in con una curva de aceleración similar a ease-in-quint */
}
.home-swiper {
  width: 100%;
  height: 100%;
}
.swiper-wrapper{
  align-items: center;
}

/* Inicialmente, todos los elementos son invisibles */
.swiper-slide {
  text-align: center;
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0; /* Configurado a 0 en lugar de 1 */
}

/* La clase .swiper-slide-active se aplicará automáticamente cuando el slide sea activo */
.swiper-slide-active {
  animation: fadeIn 4s ease forwards; /* Animación para mostrar el slide activo */
}

.swiper-button-next{
  margin-right: -120px;
  z-index: 110 !important;
}
.swiper-button-prev{
  margin-left: -120px;
  pointer-events: all;
  z-index: 11 0 !important;

}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}


span.content-span {
  /* Estilos para el span */
  font-weight: 500;
  font-size: 10px;
  font-family: Monzerrat-regular;
  letter-spacing: 4.35px;
}
h2.content-title {
  font-size: 45px;
  font-weight: 500;
  font-family: Monzerrat-medium;
  background: #ffffff;
  background: linear-gradient(to right, #e4d5d5 0%, #747474 30%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 25px;

  margin-top: 0;
  /* Estilos para el título */
}

div.content-description {
  /* Estilos para la descripción */
  color: rgba(255, 255, 255, 0.69);
  margin: 0 190px;
  margin-bottom: 50px;
  font-family: Monzerrat-regular;
  font-size: 11px;
  font-weight: 400;
  line-height: 16px;
}

.content-image {
  /* Estilos para la imagen */
  width: 120px;
  height: auto;
}

a.content-external-button {
  /* Estilos para el botón externo */
}

.content-subpage-list {
  /* Estilos para la lista de subpáginas */
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 30px;
}

.content-subpage-list .content-subpage {
  /* Estilos para cada elemento de subpágina */
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  font-size: 13px;
  margin-left: 10px;
}

.content-subpage.item-container {
  border: solid white 2px;
  border-radius: 3rem;
  padding: 8px 20px;
  z-index: 100;
  pointer-events: all;
  transition: transform 0.3s;
}
.content-subpage.item-container:hover {
  transform: scale(1.1);
  transition: transform 0.3s;
}
.content-subpage-list .content-subpage .icon {
  margin-left: -5px;
  padding: 5px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin-left: 10px;
  transition: transform 0.3s;
  border: solid white 2px;
  filter: brightness(2);
  z-index: 100;
  pointer-events: all;
  transform: rotate(90deg);
}
.content-subpage-list .content-subpage .icon:hover {
  transform: scale(1.1);
  transition: transform 0.3s;
}

#back-btn{
  position: fixed;
  display: grid;
  place-content: center;
  top: 0;
  left: 0;
  width:  40px;
  height: 40px;
  margin-top: 20px;
  margin-left: 20px;
  outline: solid white 2px;
  border-radius: 50%;
  filter: brightness(2);
  z-index: 200;
  pointer-events: all;
}
#back-btn img {
  width:  30px;
  height: 30px;
  transform: rotate(90deg);
}
#back-btn:hover{
  background-color: #ffffff22;
}
